<template>
  <div class="nearby">
   <button id="nearbyButton" style = "position: absolute; display:none; top: 8%; left: 50%;" v-on:click="open_button('nearby')">nearby</button>
   <button id="nearbyButtonWaiting" style = "position: absolute; display:block; top: 8%; left: 50%;" v-on:click="open_button('waiting')">nearby</button>
   
   <div id="nearbyModal" class="modal fade" role="dialog">
		<div class="modal-dialog">
			<!-- Modal content-->
			<div class="modal-content">
				<div class="modal-header">
					<!--<button type="button" class="close" data-dismiss="modal">&times;</button>-->
					<h4 class="modal-title">Nearby</h4>
					<div>
						<p class="pull-right">
							<button type="button" class="btn btn-primary" aria-label="Left Align" id="btn-exit-nearby" v-on:click="open_button('exitnearby')">
								<span class="glyphicon glyphicon-remove" aria-hidden="true"></span> Exit
							</button>
							<button type="button" class="btn btn-primary" aria-label="Left Align" id="btn-save-nearby" v-on:click="open_button('savenearby')">
								<span class="glyphicon glyphicon-save" aria-hidden="true"></span> Save
							</button>
						</p>
					</div>
				</div>
				<div class="modal-body">
					<span style="color:blue;font-size:1.2em">Device list:</span>
				</div>
				<div class="modal-footer">
					<div id="control_nearby_list" style="text-align:left; width: 100%;">
					
					</div>
				</div>
			</div>
		</div>
	</div>
	
	<div id="nearbyWaiting" class="modal fade" role="dialog">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<!--<button type="button" class="close" data-dismiss="modal">&times;</button>-->
					<h4 class="modal-title">Nearby</h4>
					<div>
						<p class="pull-right">
							<button type="button" class="btn btn-primary" aria-label="Left Align" id="btn-exit-nearbyWaiting"
							v-on:click="open_button('exitnearby')">
								<span class="glyphicon glyphicon-remove" aria-hidden="true"></span> Exit
							</button>
						</p>
					</div>
				</div>
				<div>
					<div class="loader">
					
					</div>
				</div>
			</div>
		</div>
	</div>
  </div>
</template>

<script>
import $ from 'jquery'
import nearby from '@/js/nearby'

const app_name = 'motechat';
const devType = '.mc';
var mms = '';
const EdgeInfo = { "DDN": "", "Owner": "", "Name": "", "Type": devType, "Tag": "", "Loc": "" };
var mcState = '';
const wsurl = 'http://boss.ypcloud.com:8081';

export default {
  name: 'nearby',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  created: function(){
    console.log("create");
	this.startmms(app_name,wsurl);
  },
  methods: {
	regwsOK (result) {
		console.log('ws: ' + result);
		mcState = 'web';
	},
	startmms (appname, wurl) {
		mms = new MMS(appname, wurl);
		mms.OpenMMS(this.regwsOK, this.regdcOK, this.wsStat, this.rcveMsg);
		console.log('mms: open: ' + wurl);
	},
	wsStat (result) {
		mcState = 'web error';
		console.log('ws state:' + result);
	},
	mmsGetDevice (cb) {
		mms.StateMMS('get', 'device', '', function (reply) {
			console.log('GetDevice reply=%s', JSON.stringify(reply));
			if (typeof cb == 'function') cb(reply);
		});
	},
	regdcOK (reply) {
		//console.log('regdOK result=%s', JSON.stringify(result));
		if (reply.ErrCode == 0) {
			EdgeInfo.DDN = reply.result.DDN;
		}
		var str = 'reg: ' + ' ' + reply.ErrMsg;
		console.log(str);
		if (reply.ErrCode == 0) {
			this.mmsGetDevice( function (reply) {
				if (reply.ErrCode == 0) {
					EdgeInfo.Owner = reply.result.EiOwner;
					EdgeInfo.Name = reply.result.EiName;
					//EdgeInfo.Type = reply.result.EiType;
					EdgeInfo.Tag = reply.result.EiTag;
					EdgeInfo.Loc = reply.result.EiLoc;
					console.log('webmain:regdOK EdgeInfo=%s', JSON.stringify(EdgeInfo));
					mcState = 'dc';
				}
			});
		}
		if( reply.ErrMsg == 'OK'){
			document.getElementById("nearbyButtonWaiting").style.display = "none";
			document.getElementById("nearbyButton").style.display = "block";
		}
	},
	mmsSendXmsg (target, data) {
		var cookies = document.cookie.split(";");
		mms.SendMMS(cookies[cookies.length-1],data,function cb(reply){});
	},
	mmsSendXmsg2 (target, data,name_target) {
		var cookies = document.cookie.split(";");
		mms.SendMMS(name_target,data,function cb(reply){});
	},
	rcveMsg (from, data) {
		if (typeof data == 'object') {
			console.log(from + ' ' + JSON.stringify(data));
		}
		else if (typeof data == 'string') {
			console.log(from + ' ' + data);
		}
	},
	openSelect(){
		this.getNearby(function(result){
			if ( result == 'OK'){
				nearby.showNearbyList();
			}
			else {
				BootstrapDialog.alert({"title":"Nearby","message":result});
			}
		});
	},
	open_button (e_type) {
		if (e_type == "setting") {
			setting(settingConfig.open);
		} else if (e_type == "waiting") {
			$('#nearbyWaiting').modal('show');
			console.log("waiting");
		} else if (e_type == "nearby") {
			//$('#nearbyWaiting').modal('show');
			this.openSelect();
			console.log("nearby");
		} else if (e_type == "exitnearby"){
			nearby.exitNearby();
		} else if (e_type == "savenearby"){
			nearby.saveNearby();
		}
	},
	getNearby(cb){
		this.mmsNearby(function(reply){
			if ( reply.ErrCode == 0 ){
				console.log('~~~~~~~~~~~~~~~~~~ ok!!!!!!');
				var dname, dtype, ei;
				var len = reply.result.length;
				var i, k;
				var str = '';
				if ( len > 0 ){
					for ( i = 0; i < len ; i++ ){
						k = i+1;
						dname = '';
						dtype = '';
						ei = reply.result[i];
						if ( ei.EiName != null ) dname = ei.EiName;
						else dname = ei.DDN;
						if ( ei.EiType != null ) dtype = ei.EiType;
						if ( dname != '' )
							str += k.toString() + '. ' + dname;
						if ( dtype != '' )
							str += ' [' + dtype + ']';
						str += '\n';
					}
					console.log(len);
				}
				else {
					str = 'no device';
					console.log(len);
				}
				nearby._nearby(reply.result,cb);
			}
			else {
				str = reply.ErrMsg;
			}
		});
	},
	mmsNearby (cb){
		mms.StateMMS('nearby', '', '', function(reply){
			if ( typeof cb == 'function' ) cb(reply);
		});
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



